import React, { useEffect, useState } from 'react';
import { Route, Switch } from 'react-router-dom';
// Components
import Header from './Header';
import AllMovies from './AllMovies';
import MyMovies from './MyMovies';
import Login from './Login';
import Profile from './Profile';
import SignUp from './SignUp';
// Services
import apiMovies from '../services/api-movies';
import apiUser from '../services/api-user';
import router from '../services/router';

const App = () => {
  // State: user
  const [userId, setUserId] = useState('');
  const [userName, setUserName] = useState('');
  const [userEmail, setUserEmail] = useState('');
  const [userPassword, setUserPassword] = useState('');
  const [userMovies, setUserMovies] = useState([]);
  // State: login
  const [loginErrorMessage, setLoginErrorMessage] = useState('');
  // State: sign up
  const [signUpErrorMessage, setSignUpErrorMessage] = useState('');
  // State: movies
  const [appMovies, setAppMovies] = useState([]);
  const [allMoviesOptionGender, setAllMoviesOptionGender] = useState('');
  const [allMoviesOptionSort, setAllMoviesOptionSort] = useState('asc');

  /*
  useEffect: obtener las pel√≠culas del API.
  Se ejecuta cuando allMoviesOptionGender o allMoviesOptionSort cambian de valor.
  Como queremos que el back devuelva las pel√≠culas filtradas por g√©nero y ordenadas por nombre estamos pasando a getMoviesFromApi estos dos valores.
  */
  useEffect(() => {
    const params = {
      gender: allMoviesOptionGender,
      sort: allMoviesOptionSort,
    };
    apiMovies.getMoviesFromApi(params).then((response) => {
      setAppMovies(response.movies);
    });
  }, [allMoviesOptionGender, allMoviesOptionSort]);

  /*
  useEffect: obtener el perfil de la usuaria.
  Se ejecuta cuando userId cambian de valor, es decir, cuando pasa de un string vac√≠o a un strin relleno con el id de la usuaria.
  Como queremos que el back devuelva los datos de una usuaria getProfileFromApi recibe el userId.
  */
  useEffect(() => {
    if (userId !== '') {
      apiUser.getProfileFromApi(userId).then((response) => {
        setUserName(response.name);
        setUserEmail(response.email);
        setUserPassword(response.password);
      });
    }
  }, [userId]);

  /*
  useEffect: obtener las pel√≠culas de la usuaria.
  Se ejecuta cuando userId cambian de valor, es decir, cuando pasa de un string vac√≠o a un strin relleno con el id de la usuaria.
  Como queremos que el back devuelva las pel√≠culas de una usuaria getUserMoviesFromApi recibe el userId.
  */
  useEffect(() => {
    if (userId !== '') {
      apiUser.getUserMoviesFromApi(userId).then((response) => {
        setUserMovies(response.movies);
      });
    }
  }, [userId]);

  /*
  Event: enviar datos del login al API.
  Con este evento enviamos los datos del login al servidor cuando la usuaria lanza el evento.
  Como queremos que el back devuelva el id de la usuaria sendLoginToApi recibe el email y la contrase√±a que ella haya escrito.
  */
  const sendLoginToApi = (loginData) => {
    // Limpiamos el error antes de enviar los datos al API
    setLoginErrorMessage('');
    // Enviamos los datos al API
    apiUser.sendLoginToApi(loginData).then((response) => {
      if (response.success === true) {
        setUserId(response.userId);
        // Si la usuaria introduce bien sus datos redireccionamos desde la p√°gina de login al inicio de la p√°gina
        router.redirect('/');
      } else {
        // Si la usuaria introduce mal sus datos guardamos el error que nos devuelve el API para que se pinte en la p√°gina
        setLoginErrorMessage(response.errorMessage);
      }
    });
  };

  /*
  Event: enviar datos del sign up (o registro) al API.
  Con este evento enviamos los datos del sign up al servidor cuando la usuaria lanza el evento.
  Como queremos que el back devuelva el id de la usuaria sendSingUpToApi recibe el email y la contrase√±a que ella haya escrito.
  */
  const sendSingUpToApi = (data) => {
    // Limpiamos el error antes de enviar los datos al API
    setSignUpErrorMessage('');
    // Enviamos los datos al API
    apiUser.sendSingUpToApi(data).then((response) => {
      if (response.success === true) {
        setUserId(response.userId);
        // Si la usuaria introduce bien sus datos redireccionamos desde la p√°gina de signup al inicio de la p√°gina
        router.redirect('/');
      } else {
        // Si la usuaria introduce mal sus datos guardamos el error que nos devuelve el API para que se pinte en la p√°gina
        setSignUpErrorMessage(response.errorMessage);
      }
    });
  };

  /*
  Event: enviar datos del profile al API.
  Con este evento enviamos los datos del profile al servidor cuando la usuaria lanza el evento.
  Le tenemos que indicar qu√© datos (nombre, email y contrase√±a) queremos enviar al API.
  Tambi√©n le tenemos que indicar cu√°l es la usuaria actual, por ello enviamos el userId
  */
  const sendProfileToApi = (userId, data) => {
    apiUser.sendProfileToApi(userId, data).then(() => {
      // Despu√©s de enviar los datos al servidor los volvemos a pedir al servidor para tenerlos actualizados
      apiUser.getProfileFromApi(userId).then((response) => {
        setUserName(response.name);
        setUserEmail(response.email);
        setUserPassword(response.password);
      });
    });
  };

  /*
  Event: cerrar sesi√≥n.
  Redireccionamos al inicio de la p√°gina.
  Recargamos la p√°gina para que se borren todos los datos del estado de React.
  */
  const logout = () => {
    router.redirect('/');
    router.reload();
  };

  /*
  Event: actualizar el g√©nero y la ordenaci√≥n.
  Aqu√≠ solo guardamos los datos en el estado.
  En el primer useEffect le decimos que cuando estos datos cambien vuelva a pedir las pel√≠culas al API.
  */
  const handleAllMoviesOptions = (data) => {
    if (data.key === 'gender') {
      setAllMoviesOptionGender(data.value);
    } else if (data.key === 'sort') {
      setAllMoviesOptionSort(data.value);
    }
  };

  // Render

  return (
    <>
      {/* Le paso a Header un booleano indicando si la usuaria est√° o no logada.
      No es necesario pasarle el userId, no necesita saberlo, le basta con saber si est√° logada o no.
      De esta forma Header maneja datos m√°s simples y solo los que necesita. Queremos que Header sea lo m√°s simple posible. */}
      <Header isUserLogged={!!userId} logout={logout} />
      <Switch>
        <Route exact path='/'>
          <AllMovies
            movies={appMovies}
            allMoviesOptionGender={allMoviesOptionGender}
            allMoviesOptionSort={allMoviesOptionSort}
            handleAllMoviesOptions={handleAllMoviesOptions}
          />
        </Route>
        <Route path='/my-movies'>
          <MyMovies movies={userMovies} />
        </Route>
        <Route path='/login'>
          <Login
            loginErrorMessage={loginErrorMessage}
            sendLoginToApi={sendLoginToApi}
          />
        </Route>
        <Route path='/signup'>
          <SignUp
            signUpErrorMessage={signUpErrorMessage}
            sendSingUpToApi={sendSingUpToApi}
          />
        </Route>
        <Route path='/profile'>
          <Profile
            userName={userName}
            userEmail={userEmail}
            userPassword={userPassword}
            sendProfileToApi={sendProfileToApi}
          />
        </Route>
      </Switch>
    </>
  );
};

export default App;
